
import React from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Progress } from "@/components/ui/progress"
import { 
  TrendingUp, 
  TrendingDown, 
  DollarSign, 
  MousePointer, 
  Eye, 
  Users,
  AlertTriangle,
  CheckCircle
} from "lucide-react"

interface CampaignMetricsProps {
  platform: string
  timeRange: string
  detailed?: boolean
}

const mockCampaigns = [
  {
    id: 1,
    name: "Campanha Emagrecimento - Meta",
    platform: "Meta Ads",
    status: "active",
    performance: "good",
    cpc: 0.85,
    ctr: 3.2,
    roi: 285,
    cpa: 45.50,
    cpm: 12.30,
    roas: 4.2,
    leads: 156,
    spend: 2450.00,
    revenue: 6987.50,
    impressions: 45230,
    clicks: 1447,
    conversions: 54
  },
  {
    id: 2,
    name: "Pets Premium - Google",
    platform: "Google Ads",
    status: "active",
    performance: "excellent",
    cpc: 1.20,
    ctr: 4.8,
    roi: 420,
    cpa: 32.80,
    cpm: 8.90,
    roas: 5.8,
    leads: 89,
    spend: 1890.00,
    revenue: 7956.30,
    impressions: 28450,
    clicks: 1366,
    conversions: 58
  },
  {
    id: 3,
    name: "Finanças Pessoais - TikTok",
    platform: "TikTok Ads",
    status: "active",
    performance: "poor",
    cpc: 2.10,
    ctr: 1.8,
    roi: 95,
    cpa: 89.20,
    cpm: 18.50,
    roas: 1.9,
    leads: 23,
    spend: 3200.00,
    revenue: 4180.00,
    impressions: 67890,
    clicks: 1222,
    conversions: 36
  }
]

const getPerformanceColor = (performance: string) => {
  switch (performance) {
    case "excellent": return "text-green-600 bg-green-50"
    case "good": return "text-blue-600 bg-blue-50"
    case "poor": return "text-red-600 bg-red-50"
    default: return "text-gray-600 bg-gray-50"
  }
}

const getPerformanceIcon = (performance: string) => {
  switch (performance) {
    case "excellent": return <CheckCircle className="h-4 w-4 text-green-600" />
    case "good": return <TrendingUp className="h-4 w-4 text-blue-600" />
    case "poor": return <AlertTriangle className="h-4 w-4 text-red-600" />
    default: return <TrendingUp className="h-4 w-4 text-gray-600" />
  }
}

export default function CampaignMetrics({ platform, timeRange, detailed = false }: CampaignMetricsProps) {
  const filteredCampaigns = platform === "all" 
    ? mockCampaigns 
    : mockCampaigns.filter(c => c.platform.toLowerCase().includes(platform))

  const totalMetrics = filteredCampaigns.reduce((acc, campaign) => ({
    spend: acc.spend + campaign.spend,
    revenue: acc.revenue + campaign.revenue,
    leads: acc.leads + campaign.leads,
    impressions: acc.impressions + campaign.impressions,
    clicks: acc.clicks + campaign.clicks,
    conversions: acc.conversions + campaign.conversions
  }), { spend: 0, revenue: 0, leads: 0, impressions: 0, clicks: 0, conversions: 0 })

  const avgMetrics = {
    cpc: totalMetrics.spend / totalMetrics.clicks,
    ctr: (totalMetrics.clicks / totalMetrics.impressions) * 100,
    roi: ((totalMetrics.revenue - totalMetrics.spend) / totalMetrics.spend) * 100,
    cpa: totalMetrics.spend / totalMetrics.conversions,
    roas: totalMetrics.revenue / totalMetrics.spend
  }

  return (
    <div className="space-y-6">
      {/* Overview Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Investimento Total</CardTitle>
            <DollarSign className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">R$ {totalMetrics.spend.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}</div>
            <p className="text-xs text-muted-foreground">
              +12.5% em relação ao período anterior
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Faturamento</CardTitle>
            <TrendingUp className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">R$ {totalMetrics.revenue.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}</div>
            <p className="text-xs text-muted-foreground">
              ROAS: {avgMetrics.roas.toFixed(2)}x
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Leads Gerados</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{totalMetrics.leads.toLocaleString('pt-BR')}</div>
            <p className="text-xs text-muted-foreground">
              CPA: R$ {avgMetrics.cpa.toFixed(2)}
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Taxa de Conversão</CardTitle>
            <MousePointer className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{((totalMetrics.conversions / totalMetrics.clicks) * 100).toFixed(1)}%</div>
            <p className="text-xs text-muted-foreground">
              CTR: {avgMetrics.ctr.toFixed(2)}%
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Detailed Campaign List */}
      {detailed && (
        <Card>
          <CardHeader>
            <CardTitle>Campanhas Detalhadas</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {filteredCampaigns.map((campaign) => (
                <div key={campaign.id} className="border rounded-lg p-4 space-y-3">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      {getPerformanceIcon(campaign.performance)}
                      <div>
                        <h3 className="font-semibold">{campaign.name}</h3>
                        <p className="text-sm text-muted-foreground">{campaign.platform}</p>
                      </div>
                    </div>
                    <Badge className={getPerformanceColor(campaign.performance)}>
                      {campaign.performance === "excellent" && "Excelente"}
                      {campaign.performance === "good" && "Boa"}
                      {campaign.performance === "poor" && "Ruim"}
                    </Badge>
                  </div>

                  <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-7 gap-4 text-sm">
                    <div>
                      <p className="text-muted-foreground">CPC</p>
                      <p className="font-semibold">R$ {campaign.cpc.toFixed(2)}</p>
                    </div>
                    <div>
                      <p className="text-muted-foreground">CTR</p>
                      <p className="font-semibold">{campaign.ctr}%</p>
                    </div>
                    <div>
                      <p className="text-muted-foreground">ROI</p>
                      <p className="font-semibold">{campaign.roi}%</p>
                    </div>
                    <div>
                      <p className="text-muted-foreground">CPA</p>
                      <p className="font-semibold">R$ {campaign.cpa.toFixed(2)}</p>
                    </div>
                    <div>
                      <p className="text-muted-foreground">ROAS</p>
                      <p className="font-semibold">{campaign.roas}x</p>
                    </div>
                    <div>
                      <p className="text-muted-foreground">Leads</p>
                      <p className="font-semibold">{campaign.leads}</p>
                    </div>
                    <div>
                      <p className="text-muted-foreground">Gasto</p>
                      <p className="font-semibold">R$ {campaign.spend.toLocaleString('pt-BR')}</p>
                    </div>
                  </div>

                  {campaign.performance === "poor" && (
                    <div className="bg-red-50 border border-red-200 rounded p-3">
                      <div className="flex items-center space-x-2 text-red-800">
                        <AlertTriangle className="h-4 w-4" />
                        <span className="font-medium">Campanha com baixo desempenho</span>
                      </div>
                      <p className="text-sm text-red-700 mt-1">
                        ROI abaixo de 100% e ROAS baixo. Considere otimizar o público-alvo ou criativo.
                      </p>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
