
import React, { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { 
  Search, 
  ExternalLink, 
  TrendingUp, 
  TrendingDown,
  Star,
  Eye,
  MousePointer,
  DollarSign
} from "lucide-react"

interface NicheSearchProps {
  timeRange: string
}

const mockNiches = ["emagrecimento", "pets", "finanças", "beleza", "fitness", "culinária", "tecnologia", "educação"]

const mockTrendingCampaigns = [
  {
    id: 1,
    productName: "Detox Slim Pro",
    niche: "emagrecimento",
    adType: "Video Carousel",
    platform: "Meta Ads",
    performance: {
      roas: 6.8,
      ctr: 4.2,
      cpc: 0.65,
      spend: 15420,
      revenue: 104856
    },
    trend: "up",
    growth: 145,
    timeframe: "5 dias"
  },
  {
    id: 2,
    productName: "Ração Premium Plus",
    niche: "pets",
    adType: "Single Image",
    platform: "Google Ads",
    performance: {
      roas: 5.2,
      ctr: 3.8,
      cpc: 1.20,
      spend: 8900,
      revenue: 46280
    },
    trend: "up",
    growth: 89,
    timeframe: "1 dia"
  },
  {
    id: 3,
    productName: "Curso Investimentos",
    niche: "finanças",
    adType: "Video",
    platform: "TikTok Ads",
    performance: {
      roas: 4.1,
      ctr: 2.9,
      cpc: 1.85,
      spend: 12300,
      revenue: 50430
    },
    trend: "down",
    growth: -12,
    timeframe: "15 dias"
  },
  {
    id: 4,
    productName: "Sérum Anti-idade",
    niche: "beleza",
    adType: "Collection",
    platform: "Meta Ads",
    performance: {
      roas: 7.2,
      ctr: 5.1,
      cpc: 0.78,
      spend: 9800,
      revenue: 70560
    },
    trend: "up",
    growth: 203,
    timeframe: "30 dias"
  }
]

export default function NicheSearch({ timeRange }: NicheSearchProps) {
  const [searchTerm, setSearchTerm] = useState("")
  const [selectedTimeframe, setSelectedTimeframe] = useState("30")
  const [filteredCampaigns, setFilteredCampaigns] = useState(mockTrendingCampaigns)

  const handleSearch = () => {
    if (searchTerm.trim()) {
      const filtered = mockTrendingCampaigns.filter(campaign => 
        campaign.niche.toLowerCase().includes(searchTerm.toLowerCase()) ||
        campaign.productName.toLowerCase().includes(searchTerm.toLowerCase())
      )
      setFilteredCampaigns(filtered)
    } else {
      setFilteredCampaigns(mockTrendingCampaigns)
    }
  }

  const handleBigSpyRedirect = (niche: string) => {
    const url = `https://bigspy.com/en/search?keyword=${encodeURIComponent(niche)}`
    window.open(url, '_blank')
  }

  const getTimeframeLabel = (timeframe: string) => {
    switch (timeframe) {
      case "1": return "Último dia"
      case "5": return "Últimos 5 dias"
      case "15": return "Últimos 15 dias"
      case "30": return "Últimos 30 dias"
      case "365": return "Último ano"
      default: return "Últimos 30 dias"
    }
  }

  return (
    <div className="space-y-6">
      {/* Search Header */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center">
            <Search className="h-5 w-5 mr-2" />
            Pesquisa por Nicho e Ranking de Campanhas
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col md:flex-row gap-4">
            <div className="flex-1">
              <Input
                placeholder="Digite o nicho (ex: emagrecimento, pets, finanças...)"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
              />
            </div>
            <Select value={selectedTimeframe} onValueChange={setSelectedTimeframe}>
              <SelectTrigger className="w-48">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="1">Último dia</SelectItem>
                <SelectItem value="5">Últimos 5 dias</SelectItem>
                <SelectItem value="15">Últimos 15 dias</SelectItem>
                <SelectItem value="30">Últimos 30 dias</SelectItem>
                <SelectItem value="365">Último ano</SelectItem>
              </SelectContent>
            </Select>
            <Button onClick={handleSearch}>
              <Search className="h-4 w-4 mr-2" />
              Pesquisar
            </Button>
          </div>

          {/* Popular Niches */}
          <div className="mt-4">
            <p className="text-sm text-muted-foreground mb-2">Nichos populares:</p>
            <div className="flex flex-wrap gap-2">
              {mockNiches.map((niche) => (
                <Badge
                  key={niche}
                  variant="outline"
                  className="cursor-pointer hover:bg-primary hover:text-primary-foreground"
                  onClick={() => {
                    setSearchTerm(niche)
                    handleSearch()
                  }}
                >
                  {niche}
                </Badge>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Trending Campaigns */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            <span>Campanhas em Alta - {getTimeframeLabel(selectedTimeframe)}</span>
            <Badge variant="secondary">{filteredCampaigns.length} campanhas encontradas</Badge>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {filteredCampaigns.map((campaign, index) => (
              <div key={campaign.id} className="border rounded-lg p-4 hover:shadow-md transition-shadow">
                <div className="flex items-start justify-between mb-3">
                  <div className="flex items-center space-x-3">
                    <div className="flex items-center justify-center w-8 h-8 bg-primary text-primary-foreground rounded-full text-sm font-bold">
                      #{index + 1}
                    </div>
                    <div>
                      <h3 className="font-semibold text-lg">{campaign.productName}</h3>
                      <div className="flex items-center space-x-2 mt-1">
                        <Badge variant="outline">{campaign.niche}</Badge>
                        <Badge variant="secondary">{campaign.platform}</Badge>
                        <Badge variant="ghost">{campaign.adType}</Badge>
                      </div>
                    </div>
                  </div>
                  
                  <div className="flex items-center space-x-2">
                    <div className={`flex items-center space-x-1 ${campaign.trend === 'up' ? 'text-green-600' : 'text-red-600'}`}>
                      {campaign.trend === 'up' ? <TrendingUp className="h-4 w-4" /> : <TrendingDown className="h-4 w-4" />}
                      <span className="font-semibold">{Math.abs(campaign.growth)}%</span>
                    </div>
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => handleBigSpyRedirect(campaign.niche)}
                    >
                      <ExternalLink className="h-4 w-4 mr-1" />
                      BigSpy
                    </Button>
                  </div>
                </div>

                <div className="grid grid-cols-2 md:grid-cols-5 gap-4 text-sm">
                  <div className="text-center p-3 bg-green-50 rounded">
                    <DollarSign className="h-4 w-4 mx-auto mb-1 text-green-600" />
                    <p className="text-muted-foreground">ROAS</p>
                    <p className="font-bold text-green-600">{campaign.performance.roas}x</p>
                  </div>
                  <div className="text-center p-3 bg-blue-50 rounded">
                    <MousePointer className="h-4 w-4 mx-auto mb-1 text-blue-600" />
                    <p className="text-muted-foreground">CTR</p>
                    <p className="font-bold text-blue-600">{campaign.performance.ctr}%</p>
                  </div>
                  <div className="text-center p-3 bg-purple-50 rounded">
                    <Eye className="h-4 w-4 mx-auto mb-1 text-purple-600" />
                    <p className="text-muted-foreground">CPC</p>
                    <p className="font-bold text-purple-600">R$ {campaign.performance.cpc}</p>
                  </div>
                  <div className="text-center p-3 bg-orange-50 rounded">
                    <TrendingUp className="h-4 w-4 mx-auto mb-1 text-orange-600" />
                    <p className="text-muted-foreground">Investimento</p>
                    <p className="font-bold text-orange-600">R$ {campaign.performance.spend.toLocaleString('pt-BR')}</p>
                  </div>
                  <div className="text-center p-3 bg-emerald-50 rounded">
                    <Star className="h-4 w-4 mx-auto mb-1 text-emerald-600" />
                    <p className="text-muted-foreground">Faturamento</p>
                    <p className="font-bold text-emerald-600">R$ {campaign.performance.revenue.toLocaleString('pt-BR')}</p>
                  </div>
                </div>

                <div className="mt-3 flex items-center justify-between text-sm text-muted-foreground">
                  <span>Escalando há {campaign.timeframe}</span>
                  <span>Última atualização: há 2 horas</span>
                </div>
              </div>
            ))}
          </div>

          {filteredCampaigns.length === 0 && (
            <div className="text-center py-8">
              <Search className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
              <h3 className="font-semibold mb-2">Nenhuma campanha encontrada</h3>
              <p className="text-muted-foreground">Tente pesquisar por outro nicho ou ajustar o período.</p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
